.header{
    padding: 70px;
    position: sticky;
    &__wrapper {
        display: flex;
        justify-content: space-between;
    }

    &__logo {
       
    }

    &__list {
        display: flex;
        justify-content: center;
        gap: 62px;
    }

    &__navbar {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 62px;
        margin-right: 50px;
    }

    &__list--item {
        height: 20px;
    }

    &__link {
        font-family: 'Rubik';
        font-style: normal;
        font-weight: 400;
        font-size: 1.125rem;
        line-height: 1.35rem;
        transition: 1s;
        color: #bc0b5e;
        &:hover{
            color: rgb(147, 141, 88);
            transition: color 1s;
        }
    }

    &__icons {
        display: flex;
        gap: 62px;
    }

    &__search-icon {
        width: 25px;
        height: 25px;
        background-image: url("../../img/header/Search.svg");
    }

    &__search-shopper {
        width: 25px;
        height: 25px;
        background-image: url("../../img/header/Buy.svg");
    }

    &__signin {
        cursor: pointer;
    }

    &__signin--button {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 49.77px;
        width: 125.07px;
        background: #FF7A50;
        box-shadow: 0px 22px 40px rgba(255, 104, 56, 0.19);
        border-radius: 5px;
        &:hover{
            background: rgb(255, 255, 255);
            border: 3px solid #ff7a50;
            transition: all .5s;
        }
        &:hover .header__signin--text{
            color: #FF7A50;
        }
    }

    &__signin--text {
        font-family: 'Rubik';
        font-style: normal;
        font-weight: 400;
        font-size: 1.125rem;
        line-height: 1.35rem;
        text-align: center;
        color: #FFFFFF;
    }
}
.logo { 
    font-family: 'Rubik';
    font-style: normal;
    font-weight: 500;
    font-size: 33px;
    line-height: 39px;
    letter-spacing: 0.05em;
    color: #363853;
    &::first-letter{
        color: #FF6838;
    }
}
.burger {
    width: 70px;
    height: 50px;
    display: none;
}
.burger-item {
    display: block;
    height: 5px;
    background-color: #333;
}
input#burger{
    display: none;
}

@media screen and (max-width: 600px){
    .burger{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .header{
        &__navbar{
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;
            flex-direction: column;
            visibility: hidden;
            transform: translateY(30%);
        }
        &__list{
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            &--item{
                line-height: 1rem;
            }
        }

    }
}
@media screen and (max-width: 800px){
    .header{
        padding: 30px;
        &__navbar{
            gap:32px;
            padding: 0 10px;

        }
    }
}
